@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@100&family=Ubuntu:ital,wght@0,300;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  box-sizing: border-box;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.fas {
    color: #fff;
}

.fas > span {
    font-family: 'Ubuntu', sans-serif;
    font-size: 15px;
    text-transform: capitalize;
}

@media screen and (max-width: 495px) {
    .fas > span {
        font-size: 12px;
    }
}
.row-footer {
    display: block;
    text-align: center;
}

.copyright {
    text-align: center;
}

.movie-title > span {
    font-weight: 600;
    font-size: 25px;
}

@media screen and (max-width: 760px) {
    .container > div {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

@media screen and (max-width: 760px) {
    .movie-title > span {
        display: flex;
        justify-content: center;
    }
}

@media screen and (max-width: 495px) {
    .movie-title > span {
        font-size: 20px;
    }
    .movie-title > p {
        font-size: 15px;
    }
    .footer-title > span {
        font-size: 15px;
    }
}
.image-container {
    width: 100%;
}

.hero-image {
    display: flex;
    position: relative;
}

.hero-image img {
    align-items: center;
    background: linear-gradient(to bottom, rgba(0,0,0,0)
    39%, rgba(0,0,0,0)
    41%, rgba(0,0,0,0.65)
    100%), #1c1c1c;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    height: 100vh;
    object-fit: cover;
    overflow: hidden;
}

.description {
    position: absolute; 
    background: rgba(0,0,0,0.65);
    max-width: 500px;
    max-height: -webkit-fit-content;
    max-height: -moz-fit-content;
    max-height: fit-content;
    top: 48%;
    bottom: 10%;
    padding: 25px;
    overflow: visible;   
}

.description > h2, span, p {
    color: #fff;
}

.description > h2 {
    font-size: 30px;
}

.description > p {
    font-size: 14px;
}

.movie-title {
    width: 85%; 
    margin: 1rem auto;
}

.load-button {
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 600px) {
    .description > h2 {
        font-size: 30px;
    }
}

@media screen and (max-width: 495px) {
    .movie-title > h2 {
        font-size: 25px;
        text-align: center;
    }
    .description {
        margin-top: 40px;
    }
    .description > h2 {
        font-size: 20px;
    }
    .description > p {
        font-size: 10px;
    }
}

.row {
    display: flex;
    flex-wrap: wrap;
    align-content: center;
}

.col-1 {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    display: flex;
    justify-content: center;
    padding: 10px;
    align-content: center;
}

.list-group {
    list-style: none; 
    padding: 0;
}

.list-group span {
    color: #000;
    text-align: center;
}

.list-group-movie {
    list-style: none; 
    padding: 0;
}

.list-group-movie label {
    display: flex;
    justify-content: center;
    font-size: 14px;
    text-align: center;
    max-width: 12rem;
    word-wrap: break-word;
    color: #000;
    font-weight: 700;
}

img {
    width: 12rem; 
    height: 300px; 
    margin: 0;
}

@media screen and (max-width: 760px) {
    .actor-card {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .list-group > li {
        margin: auto;
    }
}

@media screen and (max-width: 640px) {
    .col {
        padding: 15px;
        margin: auto;
    }
}

@media screen and (max-width:495px) {
    .list-group {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        align-items: center;
        padding: 20px;
        margin: 0;
    }
}
.register-box {
    width: 100%; 
    display: flex; 
    justify-content: center; 
    align-content: center;
    margin-top: 25px; 
    margin-bottom: 25px;
}

.form-box > label {
    display: flex;
    justify-content: center;
}

.form-box > h5, a {
    text-align: center;
}

.register-title {
    display: flex;
    justify-content: center;
}

.register-button {
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 600px) {
    .form-box > h5, a {
        text-align: center;
    }
}

@media screen and (max-width: 495px) {
    .register-title > h4 {
        font-size: 20px;
        font-weight: 600;
    }
    .form-box > h5, a {
        font-size: 15px;
        text-align: center;
    }
    .form-box > label {
        font-size: 12px;
    }
    .form-control {
        font-size: 12px;
    }
}
.login-box {
    width: 100%;
    height: 550px;
    display: flex; 
    justify-content: center; 
    align-content: center; 
    margin-top: 25px; 
    margin-bottom: 25px;
}

.login-title {
    display: flex;
    justify-content: center;
}

.login-button {
    display: flex; 
    justify-content: center;
}

.enroll-now {
    display: flex;
    justify-content: center;
}

@media screen and (max-width: 495px) {
    .login-title > h4 {
        font-size: 20px;
        font-weight: 600;
    }
    .enroll-now > h5, a {
        font-size: 15px;
    }
    .form-group > label {
        font-size: 15px;
    }
}
#fav-table {
    width: 100%;
    height: 450px;
    overflow: auto;
}

#fav-table > .table {
    width: 85%;
    margin: 3rem auto;
    text-align: center;
}

#fav-table > h1 {
    font-size: 30px;
    text-align: justify;
    margin: 3rem;
}

@media screen and (max-width: 495px) {
    #fav-table > h1 {
        font-size: 25px;
        text-align: center;
    }
    #fav-table > .table {
        font-size: 12px;
    }
    .btn {
        font-size: 12px;
    }
}
.movie-data {
    width: 85%; 
    margin: 1rem auto;
}

.movie-data > span {
    color: #000;
    font-size: 20px;
    font-weight: 600;
    display: flex;
    justify-content: center;
}

.movie-data > table{
    font-size: 14px;
    text-align: center;
}

@media screen and (max-width: 495px) {
    .movie-data > table{
        font-size: 10px;
    }
}
